import { IAuthentication, ICredentials } from './IAuthentication';

const secrets = new WeakMap();

export class BearerAuth implements IAuthentication {
  constructor(token?: string) {
    secrets.set(this, { token });
  }
  getFetchOptions(options: any): any {
    const secret = secrets.get(this);
    const { token } = secret;
    options.headers = Object.assign({ Authorization: `Bearer ${token}` }, options.headers);
    return options;
  }

  updateCredentials({ token }: ICredentials): undefined {
    secrets.set(this, { token });
    return;
  }

  getCometdHandshake(config: { ext?: any } = {}) {
    const secret = secrets.get(this);
    const { token, tfa } = secret;
    const KEY = 'com.cumulocity.authn';
    const ext = (config.ext = config.ext || {});
    ext[KEY] = Object.assign(ext[KEY] || {}, { token, tfa });
    return config;
  }

  logout(): void {
    secrets.set(this, {});
  }
}
